/**
 * World Configuration Data
 * 
 * Defines the multi-world architecture for WildTrax mobile experience.
 * Each world (Camping, Just Drive) has its own slides, FAQs, and content.
 * 
 * @see WORLD-SWITCHER.md for architecture documentation
 * @see SLYDESBUILD.md for component specifications
 */

import { SlideData, FAQItem, BusinessInfo } from './slideData'

/**
 * World configuration for navigation and content loading
 */
export interface WorldConfig {
  id: string           // Unique identifier: 'camping' | 'just-drive'
  name: string         // Display name: 'Camping' | 'Just Drive'
  slug: string         // URL slug for routing
  icon: string         // Emoji icon for UI
  description: string  // Short description for world selector
}

/**
 * Full world data including slides and FAQs
 */
export interface WorldData extends WorldConfig {
  slides: SlideData[]
  faqs: FAQItem[]
}

/**
 * Available worlds for WildTrax
 * Order determines display order in world selector
 */
export const worldConfigs: WorldConfig[] = [
  {
    id: 'camping',
    name: 'Camping',
    slug: 'camping',
    icon: '🏕️',
    description: 'Land Rover + Rooftop Tent'
  },
  {
    id: 'just-drive',
    name: 'Just Drive',
    slug: 'just-drive',
    icon: '🚗',
    description: 'Land Rover Day Hire'
  }
]

/**
 * Get world configuration by URL slug
 * Used for routing validation
 */
export function getWorldBySlug(slug: string): WorldConfig | undefined {
  return worldConfigs.find(w => w.slug === slug)
}

/**
 * Get world configuration by ID
 * Used for internal lookups
 */
export function getWorldById(id: string): WorldConfig | undefined {
  return worldConfigs.find(w => w.id === id)
}

/**
 * Get the default world (first in list)
 */
export function getDefaultWorld(): WorldConfig {
  return worldConfigs[0]
}

/**
 * Check if a slug is a valid world
 */
export function isValidWorldSlug(slug: string): boolean {
  return worldConfigs.some(w => w.slug === slug)
}

/**
 * Get all world slugs for static path generation
 */
export function getAllWorldSlugs(): string[] {
  return worldConfigs.map(w => w.slug)
}

