'use client'

import { useState } from 'react'
import { PhoneFrame } from '@/components/slyde-demo'
import { SlydeScreen } from '@/components/slyde-demo/SlydeScreen'
import { 
  campingSlides, 
  campingFAQs, 
  wildtraxBusiness 
} from '@/components/slyde-demo/slideData'

/**
 * Demo Slyde Page
 * 
 * Standalone demo showcasing the Slyde experience in a phone mockup.
 * This is the reference implementation for the Slydes.io platform.
 * 
 * Features:
 * - Phone mockup (280×580px, exact spec)
 * - Full 10-slide WildTrax experience
 * - Social action stack (Heart, FAQ, Share, Info) on every slide
 * - FAQ bottom sheet with accordion + Ask Question
 * - Info bottom sheet with hybrid slide-specific + business content
 * - AboutSheet with world switcher (opened from ProfilePill)
 * - ShareSheet with 3×3 platform grid
 * - Swipe/tap navigation
 * 
 * @see docs/SLYDESBUILD.md for full documentation
 */
export default function DemoSlydePage() {
  const [currentWorldId, setCurrentWorldId] = useState('camping')

  // Handle world switching (for future multi-world support)
  const handleWorldChange = (worldSlug: string) => {
    setCurrentWorldId(worldSlug)
    // In production, this would fetch different slides from Supabase
  }

  return (
    <main className="min-h-screen bg-black flex flex-col items-center justify-center p-8">
      {/* Header */}
      <div className="text-center mb-8">
        <h1 className="text-white text-3xl font-bold mb-2">
          Slydes Demo
        </h1>
        <p className="text-white/60 text-sm max-w-md">
          The mobile-first storytelling experience. Tap to navigate, explore the social features.
        </p>
        <p className="text-cyan-400/80 text-xs mt-2">
          ✨ Reference implementation from SLYDESBUILD.md
        </p>
      </div>

      {/* Phone Mockup */}
      <PhoneFrame enableTilt={true}>
        <SlydeScreen
          slides={campingSlides}
          faqs={campingFAQs}
          business={wildtraxBusiness}
          autoAdvance={false}
          currentWorldId={currentWorldId}
          onWorldChange={handleWorldChange}
        />
      </PhoneFrame>

      {/* Footer */}
      <div className="text-center mt-8">
        <p className="text-white/40 text-xs">
          Built with Slydes.io
        </p>
        <div className="flex items-center justify-center gap-4 mt-4">
          <a 
            href="/demo" 
            className="text-white/60 text-sm hover:text-white transition-colors"
          >
            ← Back to Demos
          </a>
          <span className="text-white/20">•</span>
          <a 
            href="/" 
            className="text-white/60 text-sm hover:text-white transition-colors"
          >
            Slydes.io
          </a>
        </div>
      </div>
    </main>
  )
}

